<template>
	<div>
		<Loader v-if="isLoading" />
		<v-data-table
			v-else
			class="mt-6"
			:headers="header"
			:items="dataPeserta"
			:search="search"
			:items-per-page="10"
			hide-default-footer>	
	    <template v-slot:item.no="{item}">
	    	{{dataPeserta.map(x => x.personal.id).indexOf(item.personal.id)+1}}    	
	    </template>	
	    <template v-slot:item.prodi="{item}">
	    	{{
	    		dataPeserta
	    			.filter(x => x.personal.id === item.personal.id)[0].ptn
	    			.map(ptn => ptn.prodi).join(',')
	    	}}    	
	    </template>	
			<template v-slot:top>
				<v-text-field
	        v-model="search"
	        prepend-icon="mdi-magnify"
	        label="Cari peserta"
	        single-line
	        autocomplete="off"
	        hide-details
	      ></v-text-field>
			</template>
			<template v-slot:item.aktif="{ item }">
	      <v-btn 
	      	style="border-radius: 2px 0 0 2px"
	      	x-small
	      	@click="item.aktif = true"
	      	:color="item.aktif === true?'success':'default'"
	      	depressed>
	      	on
	      </v-btn>
	      <v-btn 
	      	style="border-radius: 0 2px 2px 0"
	      	x-small
	      	@click="item.aktif = false"
	      	:color="item.aktif === false?'red':'default'"      	
	      	:dark="item.aktif === false?true:false" 
	      	depressed>
	      	off
	      </v-btn>
	    </template>
			<template v-slot:no-data>
	      <v-alert type="info" class="mt-4">
	        <div>Data tidak ditemukan</div>
	      </v-alert>
	    </template>
			<template v-slot:item.action="{item}">
				<v-btn icon color="success">
					<v-icon small>mdi-eye</v-icon>
				</v-btn>
				<v-btn icon color="primary">
					<v-icon small>mdi-pencil</v-icon>
				</v-btn>
				<v-btn icon color="red">
					<v-icon small>mdi-trash-can</v-icon>
				</v-btn>
			</template>		
		</v-data-table>

		<v-pagination
			v-model="page"
			:length="lengthPage"
			class="mt-3"
		></v-pagination>
	</div>
</template>

<script>
	import { mapState } from 'vuex'
	import Loader from '@/components/Loader'
	export default{
		data: () => ({
			header: [
				{ text: 'No', value: 'no', sortable: false },
				{ text: 'Nama', value: 'personal.nama', sortable: false },
				{ text: 'Sekolah Asal', value: 'sekolah.nama', sortable: false },
				{ text: 'Program Studi', value: 'prodi', sortable: false },
				{ text: 'Username', value: 'user', sortable: false },
				{ text: 'Password', value: 'password', sortable: false },
				{ text: 'Aktifasi', value: 'aktif', sortable: false },
				{ text: 'Action', value: 'action', align: 'center', sortable: false }
			],

			data: [
				{
					name: 's',
					asal: 's',
					prodi: 's',
					user: 's',
					pass: 's',
					aktivasi: true
				}
			],
			page: 1,
			search: ''
		}),

		components: {
			Loader
		},

		created() {
			this.$store.dispatch('dataPeserta/getDataPeserta', this.page)
			this.$store.dispatch('dataPeserta/updateIsLoading', true)
		},

		watch: {
			page(val) {
				this.$store.dispatch('dataPeserta/getDataPeserta', val)
				this.$store.dispatch('dataPeserta/updateIsLoading', true)
			},
		},

		computed: {
			...mapState('dataPeserta', ['dataPeserta', 'isLoading', 'lengthPage'])
		}
	}
</script>